@charset "utf-8";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
body {
    color: #333;
}
.header {
    background-color: antiquewhite;
    text-align: center;
    margin-bottom: 80px;
}
.check {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
}
.check form {
    position: relative;
}
.check form button {
    position: absolute;
    right: 0px;   
}
.check form input[type="text"] {
    width: 100%;
    height: 50px;
    border: 1px solid #999;
    padding: 10px;
}
.check form button img {
    width: 43px;
    height: 43px;
}
.check .item  {
    background-color: #d4b6b6;
    margin: 5px 0;
    
    justify-content: space-between;
    display: flex;
    align-items: center;
}
.check .item strong {
    width: 100%;
    padding-left: 10px;
}
img {
    width: 43px;
    height: 43px;
}

.item ul {
    display: flex;
    gap: 10px;
    margin-right: 10px;
    font-size: 16px;
    
}

